<template>
    <div class="main__product-box pages__style-box">
        <div class="main__product-items pages__style-items">
            <ProductItem
            :SortProducts="filterProducts"
            />
        </div>
    </div>
    <!-- nums with different adaptive -->
        <!-- for Mobile -->
          <ul class="main__product-slide pages__style-slide" 
            @click="getSliding"
            v-show="filterProducts.length >= 0"
            v-if="this.device < 540">
                <img src="@/assets/images/SVG/Icons/topArrow.svg" alt="" class="main__product-arrow pages__style-arrow prev">
                <li v-for="n in this.offsetM(filterProducts.length)" :key="n">
                    <a :class="n.class">{{ n.offset }}</a>
                </li>
                <img src="@/assets/images/SVG/Icons/bottomArrow.svg" alt="" class="main__product-arrow pages__style-arrow next">
          </ul>
          <!-- for Laptop -->
          <ul class="main__product-slide pages__style-slide" 
            @click="getSliding" 
            v-show="filterProducts.length >= 0"
            v-if="this.device < 996 && this.device > 540">
                <img src="@/assets/images/SVG/Icons/topArrow.svg" alt="" class="main__product-arrow pages__style-arrow prev">
                <li v-for="n in this.offsetN(filterProducts.length)" :key="n">
                    <a :class="n.class">{{ n.offset }}</a>
                </li>
                <img src="@/assets/images/SVG/Icons/bottomArrow.svg" alt="" class="main__product-arrow pages__style-arrow next">
          </ul>
          <!-- for Desctop -->
          <ul 
            @click="getSliding" 
            v-show="filterProducts.length >= 0" 
            class="main__product-slide pages__style-slide"  
            v-if="this.device > 996">
                <img src="@/assets/images/SVG/Icons/topArrow.svg" alt="" class="main__product-arrow pages__style-arrow prev">
                <li v-for="n in this.offsetD(filterProducts.length)" :key="n">
                    <a :class="n.class">{{ n.offset }}</a>
                </li>
                <img src="@/assets/images/SVG/Icons/bottomArrow.svg" alt="" class="main__product-arrow pages__style-arrow next">
          </ul>
</template>

<script>
            

import ProductItem from '@/components/Product/Product_Item.vue'
// few functions from storage.js 
import {getHeight, getNumsD, getNumsN, getNumsM} from '@/store/storage.js'
  
// import Firebase's configurations
import { db } from '@/store/db.js'
import { collection ,getDocs } from 'firebase/firestore'


export default {
    components:{
        ProductItem
    },
    data(){
        return {
            offsetD:getNumsD,
            offsetN:getNumsN,
            offsetM:getNumsM,
            status:false,
            interval:4,
            device: window.innerWidth,
            dots:[],
            productsFB:[]
      }
    },
    methods:{
        // Slider System
        getSliding(){
            const items = document.querySelector('.pages__style-items');
            const item = document.querySelector('.pages__style-item');
            const nums = document.querySelectorAll('.pages__style-nums');
                if(event.target.nodeName == 'P' ||event.target.nodeName == 'A'){
                    // sliding items
                let offset = 0;
                    if(window.innerWidth > 996){ // for Desctop
                        offset += item.clientHeight*2+68;
                    }else if(window.innerWidth < 996 && window.innerWidth > 540){ // for Laptop
                        offset += (item.clientHeight+20)*2;
                    }else if(window.innerWidth <= 540){ // for Mobile
                        offset += (item.clientHeight+20)*3;
                    }
                nums.forEach(el =>  el.classList.remove('active'));
                event.target.classList.add('active');
                items.style.transform = `translateY(${getHeight(event.target.innerHTML,offset)}px)` // sliding using by translateY
                    // getting new slides for sliding
                }else if(event.target.nodeName == 'IMG'){
                        if(event.target.classList[2] == 'next'){
                            this.interval = nums[nums.length-1].classList[2] == 'visible' ? this.interval : this.interval += 4;
                            for(let i =0;i<nums.length;i++){
                                if(nums[i].innerHTML <= this.interval && nums[i].innerHTML > this.interval-4){
                                    nums[i].classList.add('visible');
                                }else{
                                    nums[i].classList.remove('visible');
                                }
                            }
                        }else if(event.target.classList[2] == 'prev'){
                            this.interval = this.interval ==4 ? this.interval=4 : this.interval -= 4 ;
                            for(let i =0;i<nums.length;i++){
                                if(nums[i].innerHTML <= this.interval && nums[i].innerHTML > this.interval-4){
                                    nums[i].classList.add('visible');
                                }else{
                                    nums[i].classList.remove('visible');
                                }
                            }
                        }
                    }
                
        },
        countItems(length){
            if(length > 6){
                dots.push({
                    id:1
                })
            }
        },
    },
    created(){
        async function getProducts(product) {
            const querySnapshot = await getDocs(collection(db, "products"));
               querySnapshot.forEach((doc) => {
                product.push(doc.data());
            });
                localStorage.setItem('products', JSON.stringify(product));
        }
        getProducts(this.productsFB);
    },
    computed:{
        filterProducts(){
            // filter array of products from Firestore
            if(this.$store.getters.getChooseCategory == '1'){
               return localStorage.products ? JSON.parse(localStorage.getItem("products")).filter(product => product.category == '1' )
               : this.productsFB.filter(product => product.category == '1')
            }else if(this.$store.getters.getChooseCategory == '2'){
                return localStorage.products ? JSON.parse(localStorage.getItem("products")).filter(product => product.category == '2' )
                : this.productsFB.filter(product => product.category == '2')
            }else if(this.$store.getters.getChooseCategory == '3'){
                return localStorage.products ? JSON.parse(localStorage.getItem("products")).filter(product => product.category == '3' )
                : this.productsFB.filter(product => product.category == '3')
            }else{
                if (localStorage.products) {
                    return JSON.parse(localStorage.getItem("products")).sort((a, b) => a.id > b.id ? 1 : -1)
                }else 
                    return this.productsFB.sort((a,b) => a.id > b.id ? 1 : -1 )
            }
        }
    }
}
</script>

<style>

</style>