<template>
  <main class="main">
    <div class="container">
      <div class="main__adding">
        <h2 class="main__adding-title title">
          Добавьте свою статью
        </h2>
        <form action="#" id="contactForm" class="main__adding-form">
          <!-- TITLE -->
          <label class="main__adding-label add-title" id="add_title">
              <p class="main__adding-text text">Нзвание статьи</p>
              <input type="text" class="main__adding-input" id="title" maxlength="24" v-model="title">
          </label>
          <!-- Category -->
          <label class="main__adding-label add-category" id="add_category">
              <select name="select" id="" class="main__adding-select text">
                <option class="main__adding-option text">Выберите категорию</option>
                <option class="main__adding-option text" value="gaming">Gaming</option>
                <option class="main__adding-option text" value="development">Development</option>
                <option class="main__adding-option text" value="tips">Tips</option>
                <option class="main__adding-option text" value="fishing">Fishing</option>
                <option class="main__adding-option text" value="security">Security</option>
                <option class="main__adding-option text" value="ways">Ways</option>
              </select>
          </label>
          <!-- Image -->
          <label class="main__adding-label add-image" id="add_image">
              <p class="main__adding-text text">
                Загрузите картинку вашей статьи
              </p>
              <p class="main__adding-text micro-text">
                Желательный размер картинки: 385x230
              </p>  
              <input type="file" ref="file" class="main__adding-input" id="image" accept="image/png, image/jpg" >
          </label>
          <label class="main__adding-label add-title" id="add_title">
              <p class="main__adding-text text">Статья</p>
              <!-- <input type="text" class="main__adding-input" id="title" v-model="text"> -->
              <textarea class="main__adding-textarea" id="add_text" v-model="text"></textarea>
          </label>          
          <!-- TEXT -->
          <a class="main__adding-btn text" @click="handleFileUpload()">Вывести</a>
          <!-- <button >Вывести</button> -->
        </form>
          <!-- Article start -->
          <div class="main__adding-item" v-show="showPreview">
              <div class="main__adding-images">
                <img :src="imagePreview" alt="" class="main__adding-img">
                <div class="main__adding-images-category">
                    <img :src="require(`@/assets/images/SVG/Icons/${icon}`)" alt="" class="main__adding-icon" v-show="category != null">
                    <p class="main__adding-images-text micro-text">
                        {{category}}
                    </p>
                  </div>
                <div class="main__adding-images-eyes">
                    <img src="../assets/images/SVG/Icons/eye.svg" alt="" class="main__adding-icon">
                    <p class="main__adding-images-text micro-text">
                        24K
                    </p>
                </div>
                <div class="main__adding-images-data">
                    <img src="../assets/images/SVG/Icons/note.svg" alt="" class="main__adding-icon">
                    <p class="main__adding-images-text micro-text">
                      {{date}}
                    </p>
                </div>
              </div>
            <h2 class="main__adding-title text">{{title}}</h2>
            <p class="main__adding-descr inscr">{{text}}</p>
          </div>
          <!-- Article end -->
      </div>
    </div>
  </main>
  
</template>

<script>
 

// let options = document.querySelectorAll('.main__adding-option')
import {getImage} from '../store/db.js'

export default {
  data(){
    return{
      file:'',
      title:'',
      text:'',
      showPreview: false,
      imagePreview: '',
      category:'',
      icon:'gaming.svg',
      date:''
    }
  },
  methods:{
    // Function shows article(item)
    handleFileUpload(){
      let date = new Date()
      this.icon = document.querySelector('.main__adding-select').value + '.svg';
      this.category = document.querySelector('.main__adding-select').value;
      this.date = ''  


      function getDate(){
        let dateApp = date.getDate() < 10 ? `0${date.getDate()}.` : date.getDate()+'.'
          dateApp += date.getMonth()+1 < 10 ? `0${date.getMonth()+1}.` : date.getMonth()+1+'.'
          dateApp += date.getFullYear() < 10 ? `0${date.getFullYear()}` : date.getFullYear()
          return dateApp;
      }


      this.file = this.$refs.file.files[0];
      let reader = new FileReader();

      let image = document.getElementById('image');
      // getImage(this.$refs.file.files[0],);
      console.log(this.file);

      // getInputVal();
      
      //when file finish load, scipt show article(item) for user 
      reader.addEventListener("load", function () {
        this.showPreview = true;
          this.imagePreview = reader.result;
        }.bind(this), false);
      // check if file exists                          && this.title != ''
      if( this.file ){
        if ( /\.(jpe?g|png|gif)$/i.test( this.file.name ) ) {
          reader.readAsDataURL( this.file );
          this.date = getDate(this.date);
        }
      }



          // console.log(this.$refs.file.files[0].lastModifiedDate);
    }
  }
}
</script>

<style>

</style>